<template>
  <div class="practices">
    <!-- search input -->
    <div class="custom-search d-flex justify-content-between align-items-center">
      <b-form-group>
        <div class="d-flex align-items-center">
          <label class="mr-1">Search</label>
          <b-form-input
            v-model="searchTerm"
            placeholder="Search"
            type="text"
            class="d-inline-block"
          />
        </div>
      </b-form-group>

      <b-form-group>
        <div class="d-flex align-items-center">
          <label class="mr-1 text-nowrap">EHR System</label>
          <b-form-select
            v-model="selected"
            label="title"
            :options="filterOptions.filterDropdownItems"
          />
        </div>
      </b-form-group>

      <b-form-group>
        <div class="d-flex align-items-center">
          <label class="mr-1 text-nowrap">Assigned User</label>
          <b-form-select
            v-model="selected"
            label="title"
            :options="filterOptions.filterDropdownItems"
          />
        </div>
      </b-form-group>

      <b-form-group>
        <div class="d-flex align-items-center">
          <label class="mr-1">Status</label>
          <b-form-select
            v-model="selected"
            label="title"
            :options="filterOptions.filterDropdownItems"
          />
        </div>
      </b-form-group>

      <b-button
        v-ripple.400="'rgba(0, 207, 232, 0.15)'"
        variant="info"
        pill
      >
        <feather-icon
          icon="PlusIcon"
          class="mr-50"
        />
        <span class="align-middle">Add practice</span>
      </b-button>

    </div>

    <!-- table -->
  
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :rtl="direction"
      :search-options="{
        enabled: true,
        externalQuery: searchTerm }"
  
      :pagination-options="{
        enabled: true,
        perPage:pageLength
      }"
    >
      <template
        slot="table-row"
        slot-scope="props"
      >
        <div >
          <!-- Column: practice -->
          <span
            v-if="props.column.field === 'practice'"
          >
            <span class="text-practice">{{ props.row.practice }}</span>
            <br>
            <span class="text-id">ID: {{ props.row.id }}</span>
          </span>

          <!-- Column: Address -->
          <span v-else-if="props.column.field === 'address'">
            <span>{{ props.row.address }}</span>
          </span>

          <!-- Column: contact -->
          <div v-else-if="props.column.field === 'contact'">
            <p>{{ props.row.contact.emails }}</p>
            <p v-if="props.row.contact.phones.length">Phone: {{ props.row.contact.phones }}</p>
            <p v-if="props.row.contact.fax.length">Fax: {{ props.row.contact.fax }}</p>
          </div>

          <!-- Column: status -->
          <span v-else-if="props.column.field === 'status'">
            <b-badge :variant="statusVariant(props.row.status)">
              {{ props.row.status }}
            </b-badge>
          </span>

          <!-- Column: Actions -->
          <span @click="goTo(props.row.id)" v-else class="action-col">
              <feather-icon
                icon="EyeIcon"
                class="mr-50"
              />
              <span>{{ props.row.actions }}</span>
          </span>
        </div>
      </template>

      <!-- pagination -->
      <template
        slot="pagination-bottom"
        slot-scope="props"
      >
        <div class="pagination-bottom d-flex justify-content-between flex-wrap pt-1">
          <div class="d-flex align-items-center mb-0 text-muted">
            <span class="text-nowrap ">
              Showing 1 
            </span>
            <span class="prefix"> of </span>
            <span class="text-nowrap"> {{ props.total }} entries </span>
          </div>

          <div class="d-flex">
            <div class="d-flex align-items-center">
              <span>Show</span>
              <b-form-select
                v-model="pageLength"
                :options="['3','5','10']"
                class="mx-1"
                @input="(value)=>props.perPageChanged({currentPerPage:value})"
              />
            </div>

            <b-pagination
              :value="1"
              :total-rows="props.total"
              :per-page="pageLength"
              first-number
              last-number
              align="right"
              prev-class="prev-item"
              next-class="next-item"
              class="mb-0"
              @input="(value)=>props.pageChanged({currentPage:value})"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>
          </div>
        </div>
      </template>
    </vue-good-table>
  </div>
</template>

<script>
import 'vue-good-table/dist/vue-good-table.css'
import {
  BAvatar, BBadge, BPagination, BFormGroup, BFormInput, BFormSelect, BDropdown, BDropdownItem,
} from 'bootstrap-vue'
import { VueGoodTable } from 'vue-good-table'

import { BButton } from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import store from '@/store'
export default {
  components: {
    VueGoodTable,
    BAvatar,
    BBadge,
    BPagination,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BDropdown,
    BDropdownItem,
    BButton,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      pageLength: 5,
      dir: false,
      columnFilterFn: function(data, filterString) {
        console.log(data)
        return true
      },
      columns: [
          {
          label: 'PRACTICE',
          field: 'practice',
           filterOptions: {
            styleClass: 'class1', // class to be added to the parent th element
              enabled: true, // enable filter for this column
              filterValue: null, // initial populated value for this filter
              trigger: 'enter', //only trigger on enter not on keyup 
              
            },
        },
        {
          label: 'ADDRESS',
          field: 'address',
        },
        {
          label: 'CONTACT INFO',
          field: 'contact',
        },
        {
          label: 'STATUS',
          field: 'status',
          filterOptions: {
            styleClass: 'class1', // class to be added to the parent th element
            enabled: true, // enable filter for this column
            filterValue: null, // initial populated value for this filter
            trigger: 'enter', //only trigger on enter not on keyup 
            filterFn: this.columnFilterFn, //custom filter function that
            filterDropdownItems: ["active", "inactive"],
          },
        },
        {
          label: 'ACTIONS',
          field: 'actions',
        },
        
      ],
      filterOptions: {
        filterDropdownItems: ["Active","Inactive"], // dropdown (with selected values) instead of text input
      },
      searchTerm: '',
      selected:null
    }
  },
  methods:{
    goTo(id){
    this.$router.push('/user/'+id).catch(() => {})
    }
  },
  computed: {
    statusVariant() {
      const statusColor = {
        /* eslint-disable key-spacing */
        Active       : 'light-success',
        Inactive     : 'light-dark',
        /* eslint-enable key-spacing */
      }

      return status => statusColor[status]
    },
    direction() {
      if (store.state.appConfig.isRTL) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.dir = true
        return this.dir
      }
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.dir = false
      return this.dir
    },
    rows(){
      const result=store.getters['getUsers']
      return result.map((item)=>{
        return {
          id: item.id,
          practice: item.practice,
          address: item.addresses.join(', '),
          contact: {emails:item.email.join(', '),
           phones:item.phones.join(', '),
           fax:item.fax.join(', ')
           },
          status:item.status,
          actions:item.actions
        }
      })
    }
  },
  watch:{
    selected(){
        let found = this.columns.find((c) => {
          return c.field == 'status'
        });
        found.filterOptions.filterValue = this.selected;
    }
  },

}
</script>

